import React from 'react';

import {
	InfoBoxCloseButton,
	InfoBoxContainer,
	InfoBoxTitle,
	InfoBoxText,
} from '../../styled/InfoBox';

class WorkflowConvertToMobi extends React.Component {
	render() {
		return (
			<React.Fragment>
				<InfoBoxContainer show={this.props.show}>
					<InfoBoxCloseButton onClick={() => this.props.toggleInfoBox("workflowConvertToMobi")}/>
					<InfoBoxTitle>
						Konwersja wydania z formatu EPUB do MOBI 
					</InfoBoxTitle>

					<InfoBoxText>	
						Plik EPUB w wersji "gotowy do wydania na stronie www" dodaj do Calibre.
					</InfoBoxText>

					<InfoBoxText>	
						Zaznacz na licie ksiek w Calibre t, ktr chcesz konwertowa,
                	   	a nastpnie z menu wybierz "Konwertuj ksik".
					</InfoBoxText>

					<InfoBoxText>	
						Otworzy si okno dialogowe, w ktrym po lewej stronie na grze znajduje si
                	    rozwijalna lista z formatami wyjciowymi. Wybierz format "MOBI". Zatwierd wybr ("OK").
					</InfoBoxText>

					<InfoBoxText>	
						Po zakoczeniu konwersji pozycja na licie ksiek jest ju dostpna
                	    w dwch formatach. Zapisz teraz na dysku t ksik i nastpni przejd do tego katalogu.
					</InfoBoxText>

					<InfoBoxText>	
						W katalogu znajduje si kilka plikw. Skasuj je pozostawiajc tylko plik MOBI.
                	    Po zmianie nazwy tego pliku na tak sam jak ma EPUB (poza rozszerzeniem MOBI) plik 
                	    jest "gotowy do wydania na stronie www". 
					</InfoBoxText>
				</InfoBoxContainer>
			</React.Fragment>
		)
	}
}

export default WorkflowConvertToMobi;


