import React from 'react';

import {
	InfoBoxCloseButton,
	InfoBoxContainer,
	InfoBoxTitle,
	InfoBoxText,
} from '../../styled/InfoBox';

import {
    SectionInfo,
} from '../../styled/Section';

class WorkflowGraphicImport extends React.Component {
	render() {
		return (
			<React.Fragment>
				<InfoBoxContainer show={this.props.show}>
					<InfoBoxCloseButton onClick={() => this.props.toggleInfoBox("workflowGraphicImport")}/>
						<InfoBoxTitle>
							Pliki graficzne
						</InfoBoxTitle>

						<SectionInfo>
							Konwersja rdowych grafik do formatu PNG
						</SectionInfo>
		
						<InfoBoxText>Niezalenie od tego w jakim formacie dostarczony jest rdowy
						plik grafiki (ilustracja, zdjcie, reklama, okadka ksiki), pierwszym krokiem
						jest konwersja tego pliku do formatu PNG. W trakcie tej konwersji mona jednoczenie
						zmieni docelowy wymiar grafiki (szeroko, wysoko).
						</InfoBoxText>
						<InfoBoxText>Docelowe wymiary:
							<ul>
								<li>Reklama, okadka gwna: szeroko (width) 1818px</li>
								<li>Okadki do recenzji: wysoko (height) 587px</li>
								<li>Pozostae ilustracje: szeroko (width) 1200px</li>
							</ul>
						Pliki mona konwertowa wraz z jednoczesn zmian rozmiarw 
						na stronie <a href="https://imageresize.org/" target="blank">
						Imageresize.</a> przy ustawieniu - Quality: Best.
						</InfoBoxText>
		
						<SectionInfo>
							Import plikw PNG do Calibre
						</SectionInfo>
		
						<InfoBoxText>
							Wybierz z menu komend: Import files into book.
						</InfoBoxText>
		
						<SectionInfo>
							Nazwy plikw PNG
						</SectionInfo>
		
						<InfoBoxText>	
							Nazwy plikw html do poszczeglnych dziaw rozpoczynamy prefixem wedug klucza:
                		    <ul>
                		    	<li>"szort_" - Szortownia</li>
								<li>"stu_" - Stuswka</li>
								<li>"rym_" - Rymowisko</li>
								<li>"zagr_" - Zagraniczniak</li>
								<li>"pub_" - Publicystyka</li>
								<li>"wywiad_" - Wywiad</li>
								<li>"s_" - Subiektywnie</li>
								<li>"komiks_" - pliki do komiksu</li>
								<li>"reklama_" - reklama</li>
								<li>"patronat_" - reklama Partonat</li>
							</ul>
						</InfoBoxText>

				</InfoBoxContainer>
			</React.Fragment>
		)
	}
}

export default WorkflowGraphicImport;


